import React from "react";
import image from '../image/selfphoto.jpg';
import { Link } from 'react-router-dom';
function Page(){
    return <page>
        {/* put some white spaces in it, otherwise the h1 can't be seen. */}
        <br></br>
        <br></br>
        <h1>Writing suggestion</h1>
        <div class="menu">
        <img src={image} alt="self photo" width="100" height="100"></img>
        <Link exact to="/favpage">Favourite</Link>
        <br></br>
        <Link exact to="/">Log out</Link>
        </div>
        
        <div class="input_text">
        <label for="recommendation">Please write some words here..</label>
        <textarea type="text" name="text" rows="15" cols="60"/>
        <input type="submit" name="" value="submit" />
        </div>
        <div class="output_text">
         <label for="recommendation">recommendation</label>
         <textarea name="recommendation" rows="20" cols="60"></textarea>
         </div>
        </page>
}

export default Page;