import React from "react";

function Fav(){
    return <fav>
        <div style={{paddingTop: '100px'}}> </div>
        <div className="container mt-2 mb-2" id="toolbar">
          <div id="toolbar">
            <button className="btn btn-primary btn-lg" data-toggle="modal" data-target="#myModal">
              Add
            </button>
            {/*(Modalï¼ */}
            <div className="modal fade" id="myModal" tabIndex={-1} role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
              <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content">
                  <div className="modal-header">
                    <h5 className="modal-title" id="exampleModalLabel">New Sentence</h5>
                  </div>
                  <div className="modal-body">
                    <form>
                      <div className="mb-3">
                        <label htmlFor="message-text" className="col-form-label" />
                        <textarea className="form-control" id="message-text" defaultValue={""} />
                      </div>
                    </form>
                  </div>
                  <div className="modal-footer">
                    <button type="button" className="btn btn-default" data-dismiss="modal">Close
                    </button>
                    <button type="button" className="btn btn-primary">
                      Submit
                    </button>
                  </div>
                </div>
                {/* /.modal-content */}
              </div>
              {/* /.modal */}
            </div>
          </div>
        </div>
        <div className="container mt-2 mb-2" style={{textAlign: 'center'}}>
          <table className="table table-striped" id="table" data-toggle="table" data-pagination="true" data-search="true" data-show-coluimns="true" data-show-toggle="true" data-show-refresh="true" data-show-fullscreen="true" data-show-pagination-switch="true" data-pagination-pre-text="Previous" data-pagination-next-next="Next" data-pagination-h-align="left" data-pagination-detail-h-align="right" data-page-list="[10, 20, 30, 40, 50, All]" data-toolbar="#toolbar">
            <thead>
              <tr>
                <th colSpan={4}>Collection</th>
              </tr>
              <tr>
                <th data-field="Number" scope="col" data-sortable="true" data-switchable="true">#</th>
                <th data-field="Category" scope="col" data-sortable="true" data-switchable="true">Category</th>
                <th data-field="Detail" scope="col" data-sortable="false" data-switchable="true">Detail</th>
                <th data-field="action" data-formatter="actionFormatter">Action</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td scope="row">1</td>
                <td>xxx</td>
                <td>I have a dream. The dream is xxxxx.</td>
                <td>
                  <button className="btn btn-primary btn-lg" data-toggle="modal" data-target="#myModalDelete">
                    <i className="fas fa-trash" />
                  </button>
                  <div className="modal fade" id="myModalDelete" tabIndex={-1} role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered">
                      <div className="modal-content">
                        <div className="modal-header">
                          <button type="button" className="close" data-dismiss="modal" aria-hidden="true">
                            Ã
                          </button>
                        </div>
                        <div className="modal-body">
                          <h4>Confirm to Delete?</h4>
                        </div>
                        <div className="modal-footer">
                          <button type="button" className="btn btn-default" data-dismiss="modal">Cancel
                          </button>
                          <button type="button" className="btn btn-primary">
                            Confirm
                          </button>
                        </div>
                      </div>
                      {/* /.modal-content */}
                    </div>
                    {/* /.modal */}
                  </div>
                </td>
              </tr>
            </tbody></table></div>
    </fav>
}

export default Fav;