import React, {useState} from "react";
import Input from "./Input";
import { BrowserRouter as Link } from 'react-router-dom';
import axios from "axios";

var array = [];

function Login(){ 
    const [user, setUser] = useState({username: '', password: ''});
  
    getUser(function(result){
        array = result
    });

    function getUser(callback){
        var array = [];
        axios.get('${process.env.PORT}/users/' || 'http://localhost:8080/users/')
            .then(res => {
                if(res.data.length > 0) {
                    var i;
                    for (i=0; i < res.data.length; i++){
                    let exist_user = {username: res.data[i].username, password: res.data[i].password}
                    array.push(exist_user)
                    }
                }
                callback(array)
            })
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        var i;
        var x = true
        for(i=0; i < array.length; i++){
            if(user.username == array[i].username && user.password == array[i].password){
                console.log("I' am here")
                window.location.href = "/homepage";
                x = false
                break
            }
        }
        if(x){
            alert("Your username and password is incorrect! Please try again!");
            window.location.href = "/";
        }
    }

    const handleNameInputChange = (event) => {
        event.persist();
        setUser((values) => ({
            ...values,
            username: event.target.value,
        }))
    };

    const handlePasswordInputChange = (event) => {
        event.persist();
        setUser((values) => ({
            ...values,
            password: event.target.value,
        }))
    };

    return <login>
        <div className="container">
            <h1>User Login</h1>
            <form className="form" onSubmit={handleSubmit}>
                <Input type="text" placeholder="Username" value={user.username} onChange={handleNameInputChange}/>
                <Input type="password" placeholder="Password" value={user.password} onChange={handlePasswordInputChange}/>
                <button className="btn btn-primary" type="submit">Log in</button>
            </form>
        </div>
    </login>
} 

export default Login;
