import React, { useState } from "react"
import Input from "./Input";
import axios from 'axios';

function SignUp(){
    const [user, setUser] = useState({username: '', password: ''});
    const [submitted, setSubmitted] = useState(false);

    const showSuccess = submitted;

    const handleNameInputChange = (event) => {
        event.persist();
        setUser((values) => ({
            ...values,
            username: event.target.value,
        }))
    };

    const handlePasswordInputChange = (event) => {
        event.persist();
        setUser((values) => ({
            ...values,
            password: event.target.value,
        }))
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        
        const input = {
            username: user.username,
            password: user.password
        }

        console.log(input);

        axios.post('${process.env.PORT}/users/add' || 'http://localhost:8080/users/add', input)
            .then(res => console.log(res.data));

        setSubmitted(true);
    };

    function alertFunction() {
        alert("Success! Thank you for registering");
        // window.location = '/';
    }

    return <signup>
        <div className="container">
            <h1>User Register</h1>
            <form className="form" onSubmit={handleSubmit}>
                <Input type="text" placeholder="Username" value={user.username} onChange={handleNameInputChange} />
                <Input type="text" placeholder="Password" value={user.password} onChange={handlePasswordInputChange} />
                <button className="btn btn-primary" type="submit">Register</button>
            </form>
            {showSuccess && alertFunction()}
        </div>
    </signup>
}


export default SignUp;